<template>
    <div class="section section-search">
      <h2 class="title">SEARCH ALL POSTS</h2>
      
      <form @submit.prevent="searchAllPost">
        <label for="search" class="search">
          <input type="search" id="search" class="search-input darkmode-bg--primary darkmode-text--white dark-border--none" v-model="search">
          <span class="search-icon" @click="searchAllPost"><i class="fa-solid fa-magnifying-glass"></i></span>
        </label>
      </form>

      <span class="error" id="error"></span>
    </div>

    <main class="section section-posts">
      <div class="posts-content" v-show="postsAll">
        <h3 class="posts-new__title darkmode-text--white">POSTS NEW</h3>

        <div 
        class="posts"
        >
          <div
          v-for="(post, index) in postsAll" 
          :key="index"
          class="post darkmode-bg--primary dark-border--none"
          >
            <img 
            :src="post._embedded['wp:featuredmedia'] && post._embedded['wp:featuredmedia'][0].source_url || 'https://www.publicdomainpictures.net/pictures/280000/nahled/not-found-image-15383864787lu.jpg'"
            :alt="post._embedded['wp:featuredmedia'] && post._embedded['wp:featuredmedia'][0].title.rendered"
            class="post-img"
            >

            <div class="post__text darkmode-text--white">
              <div>
                <h3 class="post__text-title">{{ post.title.rendered || post.title }}</h3>
                <p class="post__text-paragraph" v-html="post.excerpt ? post.excerpt.rendered : post._embedded.self[0].excerpt.rendered"></p>

                <div class="tag-content" v-if="post._embedded['wp:term']">
                  <span 
                  v-for="(tag, index) in post._embedded['wp:term'][1]"
                  :key="index"
                  class="tag"
                  >
                  {{ tag.name }}
                  </span>
                </div>
              </div>

              <router-link :to="`blog/posts/${post.id}`" class="post__text-link darkmode-text--white">Leer más</router-link>
            </div>
          </div>
        </div>
      </div>

      <div v-show="!postsAll" style="text-align: center;">
        <img :src="url64('oval-dark.svg')" alt="Loader" v-show="loader == 'false'">
        <img :src="url64('oval-light.svg')" alt="Loader" v-show="loader == 'true'">
      </div>
    </main>
</template>

<script>
import { ref } from 'vue'
import { blog, searchPost } from '../services/indexServices'

export default {
  name: 'Blog',

  setup() {
    const postsAll = ref();
    const search = ref();
    const loader = ref(localStorage.getItem('darkmode'))

    const getPosts = async () => {
      try {
        const res = await blog(2)
        postsAll.value = res.data
      } catch (err) {
        document.getElementById('error').textContent = `Ocurrio un ${err}`
        console.error('Ocurrio un error', err)
      }
    }

    const searchAllPost = async () => {
      try {
        if (!search.value) return getPosts();

        const res = await searchPost(search.value);
        postsAll.value = res.data

        postsAll.value.forEach(async post => {
          await fetch(post._embedded.self[0]._links['wp:featuredmedia'][0].href)
          .then(res => res.ok ? res.json() : Promise.reject(res))
          .then(data => {
            post._embedded['wp:featuredmedia'] = [data]
          })
          .catch(err => {
            console.error(err)
            document.getElementById('error').textContent = `Hubo un error ${err}`
          })
        })
      } catch (err) {
        console.error(err)
        document.getElementById('error').textContent = `Hubo un error ${err}`
      }
    }

    const url64 =(url) => {
      return require(`../assets/${url}`)
    }

    getPosts()

    return {
      loader,
      postsAll,
      search,
      url64,


      // --- Functions
      searchAllPost,
    }
  }
}
</script>

<style lang="scss" scoped>
.error {
  display: block;
  width: 80%;
  margin: 0 auto 0;
  background: var(--red-500);
  color: var(--text-white);
  padding: 0 15px;
  text-align: center;
}

.search {
  display: flex;
  .search-input {
    width: 100%;
    height: 40px;
    border-radius: 5px;
    border: 1px solid var(--gray-400);
    background: var(--color-light-secundary);
    padding: 0 15px;
  }
  .search-icon {
    width: 40px;
    height: 40px;
    background: var(--color-light-blue-dark);
    color: var(--text-white);
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    &:active {
      transform: scale(.95);
    }
  }
}
.posts {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 25px;

  .post {
    background: var(--color-light-body);
    border-radius: 10px;
    overflow: hidden;
    border: 1px solid var(--gray-400);
    display: grid;
    grid-template-rows: auto 1fr;
    .post-img {
      width: 100%;
      height: 200px;
      object-fit: cover;
    }
    .post__text {
      padding: 15px;
      display: flex;
      flex-wrap: wrap;
      gap: 15px;

      .post__text-paragraph {
        margin-top: 10px;
        line-height: 25px;
      }

      .tag-content {
        margin-top: 10px;
        display: flex;
        gap: 5px;
        .tag {
          background: var(--gray-400);
          padding: 3px 5px;
          border-radius: 3px;
        }
      }


      .post__text-link {
        width: 100%;
        align-self: flex-end;
        text-decoration: underline;
      }
    }
  }
}
</style>