<template>
  <div>
    <main class="post">
      <div class="post-content darkmode-text--white" v-show="postData">
        <h1 class="post__title">{{ postData ? postData.title.rendered : '...' }}</h1>
        <div v-html="postData ? postData.content.rendered : '...'"></div>
      </div>

      <div v-show="!postData" class="loader">
        <img :src="require('@/assets/oval-dark.svg')" alt="Loader" v-show="loader == 'false'">
        <img :src="require('@/assets/oval-light.svg')" alt="Loader" v-show="loader == 'true'">
      </div>

      <div class="index">
        <nav id="index-nav" class="post-nav darkmode-bg--primary darkmode-text--white">
          <h3>Índice:</h3>
        </nav>
      </div>
    </main>
  </div>
</template>

<script>
import { onUpdated, ref, watchEffect } from 'vue';
import { useRoute } from 'vue-router';
import { showPost } from '../services/indexServices';
export default {
  name: 'Post',

  setup() {
    const route = useRoute()
    const postData = ref();
    const $title = ref(null);
    
    const loader = ref(localStorage.getItem('darkmode'))

    const getPost = async () => {
      const res = await showPost(route.params.ig)
      postData.value = res.data
    }

    const getTitleByNav = (data, value) => {
      if (data && value) {
        $title.value = document.querySelectorAll('.post-content h2')
        const $nav = document.getElementById('index-nav');

        const $ul = document.createElement('ul')
        
        $title.value.forEach((el) => {
          el.id = `${el.textContent.split(' ').join('_')}`
          const $list = document.createElement('li')
          const $link = document.createElement('a')

          $list.classList.add("list__link")

          $link.href = `#${el.textContent.split(' ').join('_')}`
          $link.classList.add("internal-link", "darkmode-text--white")
          $link.textContent = el.textContent

          $list.appendChild($link)

          $ul.appendChild($list)
        })

        $nav.appendChild($ul)
      }
    }

    onUpdated(() => {
      $title.value = document.querySelectorAll('.post-content h2')
    })

    const watch = watchEffect(() => {
      if ($title.value) {
        getTitleByNav(postData.value, $title.value = true)
        watch()
      }
    })

    getPost()

    return {
      loader,
      postData
    }
  }
}
</script>

<style scoped lang="scss">
.post__title {
  font-size: 3rem;
}

.post {
  width: 90%;
  margin: 50px auto 0;
  display: grid;
  gap: 50px;
  grid-template-columns: 65% 1fr;
}

.index {
  position: relative;
  .post-nav {
    background: var(--color-light-primary);
    border-radius: 10px;
    border: 1px solid var(--gray-400);
    position: sticky;
    top: 40px;
    width: 100%;
    padding: 20px;
  }
}
</style>