const post = `
  .post-content code{
    background: var(--color-dark-primary);
  }

  .post-content code::-webkit-scrollbar {
    background-color: var(--gray-600);
  }
  
  .post-content code::-webkit-scrollbar-thumb {
    background: var(--color-dark-secundary);
  }

  .post > .index li a:hover {
    background: var(--color-dark-secundary);
  }
`;

export default post