const slider = `
  .slider__title-h1 {
    color: var(--text-white);
  }

  .slider__title-h2 {
    color: var(--text-dark);
    -webkit-text-stroke: 1.3px var(--color-light-blue);
    text-shadow: 0px 0px 15px var(--color-light-blue);
  }

  .slider__paragraph {
    color: #EDF2F4 !important;
  }
`;

export default slider
