<template>
  <div>
    <div class="slider container">
      <div class="slider__text">
        <div class="slider__title">
          <h1 class="slider__title-h1">Hi, I'm</h1>
          <h2 class="slider__title-h2">Johann Meza</h2>
        </div>
        
        <p class="slider__paragraph">
          Soy desarrollador web, soy autodidacta, paciente y optimista, me gusta los desafios nuevos superandome cada día.
        </p>

        <ButtonSliderComponent 
        :url="{
          button1: 'projects',
          button2: 'contact'
        }"
        :buttonContent="{
          button1: 'VIEW PROJECTS',
          button2: 'CONTACT'
        }"
        />
      </div>

      <div class="slider__img">
        <img :src="`${path}${require('../assets/home/slider.png')}`" alt="Imagen del slider" class="slider-img">
      </div>
    </div>

    <main>
      <section class="section">
        <h3 class="title">MY SERVICES</h3>
        <div class="section-services">
          <div class="service darkmode-bg--primary darkmode-border--blue">
            <div class="service__img">
              <img :src="`${path}${require('@/assets/services/ui.svg')}`" alt="" class="service-img">
            </div>
            <span class="service__text darkmode-text--white">WEB DESIGN</span>
          </div>
          
          <div class="service darkmode-bg--primary darkmode-border--blue">
            <div class="service__img">
              <img :src="`${path}${require('@/assets/services/desktop.svg')}`" alt="" class="service-img">
            </div>
            <span class="service__text darkmode-text--white">DESARROLLO DE MARCA</span>
          </div>

          <div class="service darkmode-bg--primary darkmode-border--blue">
            <div class="service__img">
              <img :src="`${path}${require('@/assets/services/responsive.svg')}`" alt="" class="service-img">
            </div>
            <span class="service__text darkmode-text--white">SISTEMAS WEBS</span>
          </div>
        </div>
      </section>

      <section class="section">
        <h3 class="title">TECHNOLOGIES</h3>

        <div class="section-tech">
          <div class="tech__category">
            <div class="tech-category__list darkmode-bg--primary darkmode-border--blue darkmode-shadow darkmode-text--white" @click="categoryActive('frontend')">FRONTEND</div>
            <div class="tech-category__list darkmode-bg--primary darkmode-border--blue darkmode-shadow darkmode-text--white" @click="categoryActive('backend')">BACKEND</div>
            <div class="tech-category__list darkmode-bg--primary darkmode-border--blue darkmode-shadow darkmode-text--white" @click="categoryActive('design')">DESIGN</div>
          </div>

          <div class="tech__tech">
            <div 
            v-for="tech in techActive"
            :key="tech"
            class="tech__image darkmode-bg--primary darkmode-shadow darkmode-border--blue"
            >
              <img :src="assetsURL(tech)" :alt="tech" :title="tech" class="tech-img">
            </div>
          </div>
        </div>
      </section>

      <ContactComponent />
    </main>
  </div>
</template>

<script>
import ContactComponent from "../components/ContactComponent.vue"
import ButtonSliderComponent from "../components/ButtonSliderComponent.vue"

export default {
  name: 'Home',

  components: { ContactComponent, ButtonSliderComponent },

  data() {
    return {
      path: 'https://johannmeza.github.io/portfolio',
      tech: {
        frontend: ['html5', 'javascript', 'css', 'vuejs', 'reactjs', 'pug', 'sass', 'tailwind', 'bulma', 'bootstrap', 'gulp'],
        backend: ['mysql', 'nodejs', 'mongodb', 'laravel', 'php'],
        design: ['adobe-xd', 'adobe-illustrator', 'adobe-photoshop', 'corel-draw'],
      },

      techActive: null
    }
  },

  methods: {
    url64(url) {
      return require(`../assets/language/${url}.png`)
    },

    assetsURL(tech) {
      return require(`../assets/language/${tech}.png`)
    },

    categoryActive(category) {
      if (this.tech[category] !== this.techActive)
        this.techActive = this.tech[category]
    }
  },

  mounted() {
    this.techActive = this.tech.frontend
  }
}
</script>

<style scoped lang="scss">
@import '../sass/breakpoint.scss';

// --- Section
.section-services {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 25px;
  .service {
    width: 100%;
    background: var(--color-light-primary);
    border: 1px solid var(--gray-400);
    border-radius: 10px;
    padding: 25px 0;
    overflow: hidden;
    .service__img {
      height: 200px;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        width: 60%;
        filter: grayscale(1);
      }
    }
    .service__text {
      padding: 15px 0;
      display: block;
      text-align: center;
    }

    .service-img {
      transition: transform .5s ease;
    }

    &:hover {
      background-color: var(--color-light-secundary);
      .service-img {
        transform: scale(1.25);
        filter: grayscale(0);
      }
      .service__text {
        color: var(--text-blue-high);
      }
    }
  }
}

// --- Technologies
.section-tech {
  width: 80%;
  margin: 0 auto 0;
  display: grid;
  grid-template-columns: 30% 1fr;
  gap: 25px;
  .tech__category {
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
    & > .tech-category__list {
      width: 100%;
      height: 80px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      border: 1px solid var(--gray-400);
      background: var(--color-light-primary);
      cursor: pointer;
      &:hover {
        background: var(--color-light-secundary);
      }
    }
  }

  .tech__tech {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(70px, 70px));
    align-content: flex-start;
    gap: 15px;
    .tech__image {
      width: 70px;
      height: 70px;
      background: var(--color-light-primary);
      border: 1px solid var(--gray-400);
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      animation-name: tech;
      animation-duration: 1s;
      animation-timing-function: ease;
      &:hover {
        background: var(--color-light-secundary);
      }
    }

    @keyframes tech {
      from {
        opacity: 0;
      }

      to {
        opacity: 1;
      }
    }
  }
  
}



</style>