const navbar = `
  body {
    background: no-repeat top / 100% url('./assets/Ellipse.svg');
    background-color: var(--color-dark-body);
    background-attachment: fixed;
  }

  #app {
    background-color: var(--color-dark-background);
  }

  body::-webkit-scrollbar {
    background-color: var(--color-dark-body);
  }
  
  body::-webkit-scrollbar-thumb {
    background: var(--color-dark-primary);
  }

  .slider-bg {
    fill: var(--color-dark-third) !important;
  }

  #slider-svg {
    fill: var(--color-dark-third) !important;
  }

  .title {
    color: var(--text-blue);
    text-shadow: 0 0 15px var(--text-blue);
  }

  .dark-border--none {
    border: none !important;
  }

  .personality__content {
    border: none !important;
    outline: 1px solid var(--color-dark-blue) !important;
    box-shadow: 0 0 5px var(--color-dark-blue);
  }


  .darkmode-bg--primary {
    background: var(--color-dark-primary) !important;
  }

  .darkmode-bg--secundary {
    background: var(----color-dark-secundary);
  }

  .darkmode-border--blue {
    border: none !important;
  }

  .darkmode-border--blue:hover {
    outline: 1px solid var(--color-dark-blue) !important;
    box-shadow: 0 0 5px var(--color-dark-blue);
  }
  

  .darkmode-text--white {
    color: var(--text-white);
  }

  .tag-content .tag {
    border: 1px solid var(--text-white);
    background: none !important;
  }
`;

export default navbar;