import { axiosConfig as axios } from './axios';

export const posts = async () =>
  await axios.get(`/posts?_embed`)

export const blog = async (id) =>
  await axios.get(`/posts?_embed&categories=${id}`)

export const showPost = async (post) => 
  await axios.get(`/posts/${post}`)

export const searchPost = async(tag) => 
  await axios.get(`/search?_embed&search=${tag}`)
  
  // await axios.get(`/search?_embed&search=${post}`)