<template>
  <aside class="section contact darkmode-bg--secundary dark-border--none">
    <h4 class="darkmode-text--white">CONTACT ME</h4>

    <form action="" class="form" @submit.prevent="handleSubmit">
      <div class="form__box">
        <label for="" class="form__label darkmode-text--white">NAME</label>
        <input type="text" class="form__input darkmode-bg--primary darkmode-text--white" v-model="form.name" required>
      </div>

      <div class="form__box">
        <label for="" class="form__label darkmode-text--white">EMAIL</label>
        <input type="email" class="form__input darkmode-bg--primary darkmode-text--white" v-model="form.email" required>
      </div>

      <div class="form__box">
        <label for="" class="form__label darkmode-text--white">BUSINESS</label>
        <textarea name="" id="" class="form__textarea darkmode-bg--primary darkmode-text--white" v-model="form.business" required></textarea>
      </div>

      <div v-show="postData" class="loader">
        <img :src="`${path}${require('@/assets/oval-dark.svg')}`" alt="Loader" v-show="loader == 'false'">
        <img :src="`${path}${require('@/assets/oval-light.svg')}`" alt="Loader" v-show="loader == 'true'">
      </div>

      <div class="form__box">
        <button class="form__btn darkmode-bg--primary">SEND</button>
      </div>
    </form>
  </aside>
</template>

<script>
import { ref } from "vue";

export default {
  name: 'Contact',

  setup() {
    const path = 'https://johannmeza.github.io/portfolio';

    const loader = ref(localStorage.getItem('darkmode'))
    const form = {};
    let postData = ref(false)

    const handleSubmit = () => {
      postData.value = true
      fetch('https://formsubmit.co/ajax/johanmezasalazar@gmail.com', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        },
        body: JSON.stringify(form)
      })
      .then(() => {
        postData.value = false
        alert('Formulario enviado');
      })
      .catch(err => {
        console.error(err)
      })
    }

    return {
      path,
      loader,
      form,
      postData,
      handleSubmit
    }
  }
  
}
</script>