<script>
import ContactComponent from "../components/ContactComponent.vue"
import ButtonSliderComponent from "../components/ButtonSliderComponent.vue"

export default {
  name: 'AboutMe',

  components: { ContactComponent, ButtonSliderComponent },


  data() {
    return {
      path: 'https://johannmeza.github.io/portfolio',

      $btnLeft: null,
      $btnRight: null,
      $carouselInner: null,
      heightCarousel: null,
      totalItem: null,
      left: null,
    }
  },

  methods: {
    moveCarousel(direction) {
      if (direction == 'left') {
        if (0 < Math.abs(this.left)) {
          this.left += this.heightCarousel
        }
      } else if (direction == 'right') {
        if (this.heightCarousel * this.totalItem > Math.abs(this.left)) {
          this.left += -this.heightCarousel
        }
      }
      document.getElementById('carousel-inner').style.left = `${this.left}px`
    },

    changeImage(id) {
      if (!id) return;

      const $section = event.target.closest('.section-personality');
      const $list = event.target.closest('.personality__list').querySelector('img')
      const $main = $section.querySelector('#personalityMain');

      if ($main.querySelector('img').src !== $list.src)
        $main.querySelector('img').src = $list.src

      document.getElementById(id).textContent = event.target.closest('.personality__list').dataset.text
    },
  },

  mounted() {
    this.$btnLeft = document.getElementById('btnleft');
    this.$btnRight = document.getElementById('btnright');

    const $carousel = document.getElementById('carousel')

    this.heightCarousel = $carousel.offsetWidth
    this.totalItem = $carousel.querySelectorAll('.carousel-item').length - 1
    this.left = 0;

    this.moveCarousel()
  }
}
</script>

